
import Tilt from 'react-tilt';
import light from './smart-energy.png'
import './Logo.css';


const Logo = () => {
	return (
		<div className='ma4 mt'>
			<Tilt className="Tilt br2 shadow-2" options={{ max : 100 }} style={{ height: 150, width: 150 }} >
			 	<div className="Tilt-inner pa3"><img style={{paddingTop: '5px'}}alt='logo' src={light}/></div>
			</Tilt>
		</div>
		);
}

export default Logo;